.profile__info-avatar-btn {
  position: relative;
  cursor: pointer;
  border-radius: 50%;
  margin-right: 30px;
  padding: 0;
  border: none;
  background: rgb(0, 0, 0);
}

.profile__info-avatar-btn::after {
  content: "";
  left: 0;
  top: 0;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  transition: opacity 1s linear;
}

.profile__info-avatar-btn:hover.profile__info-avatar-btn::after {
  background: rgba(0, 0, 0, 0.8) url(../../../icons/edit-pencil-icon.svg) 26px
    26px no-repeat;
  background-position: center;
  opacity: 1;
  transition: opacity 1s linear;
}

@media screen and (max-width: 767px) {
  .profile__info-avatar-btn {
    margin-right: 0;
    margin-bottom: 26px;
  }
}
